* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding-top: 100px;
  padding-bottom: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
}

.search-form {
  width: 100%;
  height: 70px;
  background: #00f;
  justify-content: center;
  gap: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  position: fixed;
  top: 4px;
  left: 0;
}

input {
  width: 250px;
  border: 2px solid #ccc;
  border-radius: 6px;
  padding: 10px;
}

.search {
  cursor: pointer;
  text-shadow: 0 1px #fe6;
  background: #ffec64 linear-gradient(#ffec64 5%, #ffab23 100%);
  border: 1px solid #fa2;
  border-radius: 6px;
  padding: 6px 24px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  box-shadow: inset 0 1px #fff6af;
}

.search:hover {
  background: #ffab23 linear-gradient(#ffab23 5%, #ffec64 100%);
}

.load-more {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #5dbb60;
  border: none;
  border-radius: 10px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 30px;
  font-size: 24px;
  text-decoration: none;
  transition: background-color .25s ease-in-out, transform .25s ease-in-out;
  display: block;
  transform: scale(1);
  box-shadow: 0 3px 8px rgba(0, 0, 0, .3);
}

.load-more:hover {
  background-color: #2f8f32;
  transform: scale(1.1);
}

.gallery {
